import axios from 'axios';

export default (code, langId,input) => {
  const payload = {
    code:code,
    langId:langId,
    input:input
  };
  return dispatch => {
    const url = ' https://young-garden-54430.herokuapp.com/eval';
    axios.post(
      url,
      payload
    ).then(response => {
      dispatch({
        type: 'CODE_EVALUATION_SUCCESS',
        payload: response.data
      });
    }).catch(error => {
      console.log(error);
    });
  };
};



